<template>
  <div id="app">
    <div class="content-wraper" ref="contentWrap">
      <vcontent :currentContentIndex="currentContentIndex"></vcontent>
    </div>
    <div class="nav-wraper">
      <vnav :currentNavIndex="currentContentIndex" @getCurrentContentIndex="updateCurrentIndex"></vnav>
    </div>
  </div>
</template>

<script>
import './common/css/reset.css'
import Content from './components/Content/Content.vue'
import Nav from './components/Nav/Nav.vue'
import BScroll from 'better-scroll'
let contents = document.getElementsByClassName('content-hook')
let delayId = 0
export default {
  data () {
    return {
      scrollStartY: 0,
      scrollEndY: 0,
      currentContentIndex: 0
    }
  },
  components: {
    vcontent: Content,
    vnav: Nav
  },
  created () {
    this.$nextTick(() => {
      this._initScroll()
    })
    document.addEventListener('mousewheel', (event) => {
      if (delayId) {
        clearTimeout(delayId)
      }
      delayId = setTimeout(() => {
        if (event.wheelDelta > 0) {
          if (this.currentContentIndex < 5) {
            this.currentContentIndex++
          }
        } else {
          if (this.currentContentIndex > 0) {
            this.currentContentIndex--
          }
        }
        this.contentWrapScroll.scrollToElement(contents[this.currentContentIndex])  
      }, 250)     
    })
    document.addEventListener('DOMMouseScroll', (event) => {
      if (delayId) {
        clearTimeout(delayId)
      }
      delayId = setTimeout(() => {
        if (event.detail < 0) {
          if (this.currentContentIndex < 5) {
            this.currentContentIndex++
          }
        } else {
          if (this.currentContentIndex > 0) {
            this.currentContentIndex--
          }
        }
        this.contentWrapScroll.scrollToElement(contents[this.currentContentIndex])  
      }, 250)
    })
  },
  methods: {
    _initScroll () {
      this.contentWrapScroll = new BScroll(this.$refs.contentWrap, {
        probeType: 3,
        swipeTime: 10,
        // 关闭动量动画
        momentum: false
      })
      this.contentWrapScroll.on('scrollStart', () => {
        this.scrollStartY = this.currentContentIndex * window.innerHeight
      })
      this.contentWrapScroll.on('scrollEnd', (pos) => {
        this.scrollEndY = Math.abs(pos.y)
        if ((this.scrollEndY - this.scrollStartY) > window.innerHeight / 2) {
          this.currentContentIndex += 1
        } else if ((this.scrollEndY - this.scrollStartY < -window.innerHeight / 2)) {
          this.currentContentIndex -= 1
        }
        this.contentWrapScroll.scrollToElement(contents[this.currentContentIndex])
      })
    },
    updateCurrentIndex (index) {
      this.currentContentIndex = index
      this.contentWrapScroll.scrollToElement(contents[this.currentContentIndex])
    }
  }
};
</script>

<style type="text/css" scoped>
  .content-wraper{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
  }
  .nav-wraper{
    position: fixed;
    top: 2.666667rem;
    right: 0.133333rem;
  }
</style>



