<template>
	<div class="home">
		<div class="pic"></div>
		<transition enter-active-class="animated jello">
			<div class="motto" v-show="showMotto">
				倘若你想达成目标，便得在心中描绘出目标达成后的景象，那么，梦想必会成真！
			</div>
		</transition>
		<div class="info">
			<div class="name">罗兵</div>
			<div class="occupation">前端开发工程师</div>
			<div class="mail">449037345@qq.com</div>
		</div>
	</div>	
</template>
<script type="text/javascript">
export default{
  props: {
  	currentContentIndex: {
  		type: Number,
  		default: 0
  	}
  },
  computed: {
  	showMotto () {
  		let result = false
  		if (this.currentContentIndex === 0) {
  			result = true
  		} 
  		console.log(result)
  		return result
  	}
  }  
};
</script>
<style type="text/css" scoped>
	@media screen and (min-width: 1024px){
		.pic{
			width: 150px;
			height: 150px;
			border-radius: 50%;
			margin: 100px auto 0;
			background:url(../../common/images/photo.jpg) no-repeat top center;
			background-size: 100%;
		}
		.motto{
			width: 70%;
			line-height: 50px;
			height: 50px;
			font-size: 22px;
			color: #fff;
			text-align: center;
			margin: 30px auto;
			border-bottom: 1px solid #fff;
		}
		.info div{
			width: 300px;
			height: 40px;
			margin: 0 auto;
			line-height: 40px;
			text-align: center;
			color: #fff;
			font-size: 14px;
		}
	}
</style>



