<template>
	<ul class="nav-list">
		<li v-for="(item, index) in navList" :key="index" @click="sendCurrentContentIndex(index)">
			<span v-show="index !== currentNavIndex" class="nav-item"></span>
			<transition name="icon" @before-enter="beforeEnter">
				<div v-show="index === currentNavIndex" class="icon">
					<span class="message">{{messages[index]}}</span>
					<span class="iconfont  iconfont-hook" :class="iconContent[index]"></span>
				</div>
			</transition>
		</li>
	</ul>
</template>
<script type="text/javascript">
export default{
  data () {
    return {
      navList: ['home', 'about', 'skill', 'experience', 'demo', 'contact'],
      iconContent: ['icon-weibiaoti1', 'icon-wo', 'icon-drxx14', 'icon-gongzuojingli', 'icon-zuopinzhanshi', 'icon-lianxiwomen'],
      messages: ['首页', '关于我', '技能栈', '经历', '作品集', '联系我']
    }
  },
  props: {
    currentNavIndex: {
	  type: Number,
	  default: 0
    }
  },
  methods: {
  	sendCurrentContentIndex (index) {
      this.$emit('getCurrentContentIndex', index)
  	},
  	beforeEnter (el) {
  		el.firstElementChild.style.display = 'none'
  		el.addEventListener('transitionend', () => {
  			setTimeout(() => {
  			  /* eslint-disable no-unused-vars */
  			  let refresh = el.offsetHeight
  			  el.firstElementChild.style.display = 'inline-block'
  			}, 30)
  		})
  	}
  }
};
</script>
<style type="text/css" scoped>
	@media screen and (min-width: 1024px){
		.nav-list{
			margin-top: 70px;
		}
		.nav-list li{
			height: 60px;
			line-height: 60px;
			width: 100px;
			cursor: pointer;
			text-align: center;
			font-size: 0;
		}
		.nav-item{
			display: inline-block;
			width: 15px;
			height: 15px;
			border-radius: 50%;
			background-color: rgba(0, 0, 0, 0.3);
		}
		.currentNav{
			background-color: orange;
		}
		.icon{
			display: inline-block;
			width: 30px;
			height: 30px;
			line-height: 30px;
			border-radius: 50%;
			background-color: rgb(70, 109, 226);
			vertical-align: top;
		}
		.message{
			position: absolute;
			left: -25px;
			width: 48px;
			height: 30px;
			line-height: 30px;
			text-align: center;
			background-color: rgba(255, 255, 255, 0.6);
			font-size: 12px;
			display: none;
			border-radius: 5px;
			vertical-align: top;
		}
		.nav-list li:nth-child(1) .message{
			display: inline-block;
		}
		.nav-list li:nth-child(1) .iconfont .nav-list li:nth-child(3) .iconfont{
			font-size: 16px;
		}
		.iconfont{
			font-size: 24px;
		}
		.message{
			font-size: 12px;
		}
		.icon-enter-active{
			transition: all 0.5s linear;
		}
		.icon-enter{
			transform: rotate(360deg);
		}
	}
</style>



