<template>
	<div class="skill">
		<div class="title">技能栈</div>
		<div class="star-wraper">
			<vstar></vstar>
		</div>
		<transition enter-active-class="animated bounceInRight">
			<div class="skill-desc" v-show="showSkillDesc">
				<ul>
					<li>注重效率，偏爱敏捷开发</li>
					<li>喜欢尝试，好看的页面一定要动手去实现</li>
					<li>行路有良友，身边有一群热爱前端的挚友</li>
					<li>迷恋模块化编程</li>
				</ul>
			</div>
		</transition>
	</div>	
</template>
<script type="text/javascript">
import Star from '../Star/Star.vue'
export default{
  components: {
    vstar: Star
  },
  props: {
  	currentContentIndex: {
  		type: Number,
  		default: 0
  	}
  },
  computed: {
  	showSkillDesc () {
  		let result = false
  		if (this.currentContentIndex === 2) {
  			result = true
  		} 
  		console.log(result)
  		return result
  	}
  }
};
</script>
<style type="text/css" scoped>
@media screen and (min-width: 1024px){
	.skill{
		font-size: 0;
	}
	.title{
		margin: 50px auto 0;
		text-align: center;
		font-size: 24px;
		color: #fff;
		font-weight: 600;
	}
	.star-wraper{
		display: inline-block;
		vertical-align: top;
		width: 400px;
		height: 400px;
		margin: 60px 0 30px 200px;
	}
	.skill-desc{
		display: inline-block;
		vertical-align: top;
		margin-top: 60px;
		font-size: 16px;
		width: 600px;
		height: 400px;
		color: #fff;
		box-sizing: border-box;
		padding-top: 100px;	
	}
	.skill-desc li{
		height: 50px;
		line-height: 50px;
		text-align: center;
	}		
}
</style>



