import Vue from 'vue'
import Router from 'vue-router'
import experienceOne from '../components/experienceOne/experienceOne.vue'
import experienceTwo from '../components/experienceTwo/experienceTwo.vue'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/experienceOne',
      component: experienceOne
    }, {
      path: '/experienceTwo',
      component: experienceTwo		
    }, {
      path: '/',
      redirect: '/experienceOne'
    }
  ]
})



