<template>
    <div>
      <div class="content content-hook home-wraper">
        <vhome :currentContentIndex="currentContentIndex"></vhome>
      </div>
      <div class="content content-hook about-wraper">
        <vabout :currentContentIndex="currentContentIndex"></vabout>
      </div>
      <div class="content content-hook skill-wraper">
        <vskill :currentContentIndex="currentContentIndex"></vskill>
      </div>
      <div class="content content-hook experience-wraper">
        <vexperience></vexperience>
      </div>
      <div class="content content-hook demo-wraper">
        <vdemo></vdemo>
      </div>
      <div class="content content-hook contact-wraper">
        <vcontact></vcontact>
      </div>
    </div>
</template>

<script type="text/javascript">
import Home from '../Home/Home.vue'
import About from '../About/About.vue'
import Skill from '../Skill/Skill.vue'
import Experience from '../Experience/Experience.vue'
import Demo from '../Demo/Demo.vue'
import Contact from '../Contact/Contact.vue'
export default {
  props: {
    currentContentIndex: {
      type: Number,
      default: 0
    }
  },
  mounted () {
    let height = window.innerHeight
    let width = window.innerWidth
  	for (let i = 0; i < 6; i++) {
      let content = document.getElementsByClassName('content-hook').item(i)
      content.style.width = width + 'px'
      content.style.height = height + 'px'
    }  
  },
  components: {
    vhome: Home,
    vabout: About,
    vskill: Skill,
    vexperience: Experience,
    vdemo: Demo,
    vcontact: Contact
  }
};
</script>

<style type="text/css" scoped>
  .home-wraper{
    background-color: #87b0a5;
    overflow: hidden;
  }
  .about-wraper{
    background-color: #109085;
    overflow: hidden;
  }
  .skill-wraper{
    background-color: #4d5e8f;
    overflow: hidden;
  }
  .experience-wraper{
    background-color: #945c4c;
    overflow: hidden;
  }
  .demo-wraper{
    background-color: #4b85a0;
    overflow: hidden;
  }
  .contact-wraper{
    background-color: #a29971;
    overflow: hidden;
  }
</style>



