<template>
	<div class="about">
		<div class="title">关于我</div>
		<div class="about-desc">
			<div>
				<div class="icon-wraper">
					<div class="circle-fade circle-fade-1"></div>
					<div class="circle-fade circle-fade-2"></div>
					<div class="icon">
						<span class="iconfont icon-nianling"></span>
					</div>
				</div>
				<div class="text">
					<span>年龄/28</span>
				</div>		
			</div>
			<div>
				<div class="icon-wraper">
					<div class="circle-fade circle-fade-1"></div>
					<div class="circle-fade circle-fade-2"></div>
					<div class="icon">
						<span class="iconfont icon-xueli"></span>
					</div>
				</div>
				<div class="text">
					<span>学历/全日制本科</span>
				</div>	
			</div>
			<div>
				<div class="icon-wraper">
					<div class="circle-fade circle-fade-1"></div>
					<div class="circle-fade circle-fade-2"></div>
					<div class="icon">
						<span class="iconfont icon-coordinate"></span>
					</div>
				</div>
				<div class="text">
					<span>坐标/星沙</span>
				</div>	
			</div>
			<div>
				<div class="icon-wraper">
					<div class="circle-fade circle-fade-1"></div>
					<div class="circle-fade circle-fade-2"></div>
					<div class="icon">
						<span class="iconfont icon-zhuangtai"></span>
					</div>
				</div>
				<div class="text">
					<span>状态/随时到岗</span>
				</div>	
			</div>
		</div>
		<transition enter-active-class="animated bounceInLeft">
			<div class="summary" v-show="showSummary">
				<p>一年全职前端开发经验</p>
				<p>精通div + css布局，曾经是一个高效的切图仔</p>
				<p>掌握JavaScript（jQuery、Ajax）</p>
				<p>熟悉模块化编程，掌握Webpack、Vue框架</p>
				<p>熟悉UI框架（Bootstrap、Lay-ui）</p>
				<p>而且，我没有参加过任何培训班，这都是我自学的！</p>
			</div>
		</transition>
	</div>	
</template>
<script type="text/javascript">
export default{
  props: {
  	currentContentIndex: {
  		type: Number,
  		default: 0
  	}
  },
  computed: {
  	showSummary () {
  		let result = false
  		if (this.currentContentIndex === 1) {
  			result = true
  		} 
  		console.log(result)
  		return result
  	}
  }
};
</script>
<style type="text/css" scoped>	
	@media screen and (min-width: 1024px){
		.title{
			margin: 50px auto 30px;
			text-align: center;
			font-size: 24px;
			color: #fff;
			font-weight: 600;
		}
		.about-desc{
			font-size: 0;
			text-align: center;
			color: #fff;
		}
		.about-desc>div{
			display: inline-block;
			vertical-align: top;
		}
		.icon-wraper{
			position: relative;
			width: 150px;
			height: 150px;
			border-radius: 50%;
			line-height: 150px;
			background-color: rgba(255, 255, 255, 0.1);
			margin: 10px 30px;
			cursor: pointer;
		}
		.iconfont{
			font-size: 48px;
		}
		.text{
			font-size: 16px;
		}
		.icon-wraper>div{
			position: absolute;
			border-radius: 50%;
			box-sizing: border-box;
		}
		.icon{
			width: 100%;
			height: 100%;
		}
		.circle-fade{
			width: 95%;
			height: 95%;
			margin: 2.5%;
			border:1px solid rgba(255, 255, 255, 0);	
		}
		.icon-wraper:hover .circle-fade{
			border-color: rgba(255, 255, 255, 1);
			transition: all 1s linear;
			opacity: 0;	
		}
		.icon-wraper:hover .circle-fade-1{
			transform: scale(1.8, 1.8);
		}
		.icon-wraper:hover .circle-fade-2{
			transform: scale(1.3, 1.3);
		}
		.icon-wraper:hover{
			transform: scale(0.98, 0.98);
		}
		.summary{
			width: 600px;
			margin: 30px auto 0;
		}
		.summary p{
			text-align: center;
			height: 40px;
			line-height: 40px;
			font-size: 14px;
			color: #fff;
		}
		/*.summary-enter-active{
			transition: all 1s linear;
		}
		.summary-enter{
			transform: translate(-300px, 0);
		}*/
	}
</style>



